/*=======================END-MEDIA=====================*/
/* media breakpoint mixin */
.tabs {
  background: #fff;
  border-radius: 32px;
  padding: 8px;
}

@media only screen and (min-width: 1000px) {
  .tabs {
    display: flex;
    border-radius: 72px;
  }
}

@media only screen and (max-width: 1000px) {
  .tabs, .tabs__btn {
    width: 100%;
  }
}

.tabs__btn {
  display: flex;
  gap: 8px;
  padding: 10px 24px;
  border-radius: 40px;
  border: 1px solid rgba(255, 255, 255, 0);
  font-weight: 500;
  line-height: 1.14;
  letter-spacing: -.08em;
  transition-timing-function: ease-in-out;
  transition-duration: 300ms;
  transition-property: border-color;
}

@media only screen and (max-width: 1000px) {
  .tabs__btn {
    justify-content: center;
  }
}

@media (any-hover: hover) {
  .tabs__btn:hover {
    border-color: #4C57FB;
  }
}

.tabs__btn svg {
  flex-shrink: 0;
}

.tabs__btn--active {
  border-color: #4C57FB;
  background: #4C57FB;
  color: #fff;
}

.card {
  padding: 24px;
  padding-bottom: 0px;
  border-radius: 16px;
  box-shadow: 0 8px 24px 0 rgba(81, 85, 195, 0.08);
  background: #fff;
  overflow: hidden;
}

.card--bottom-pading {
  padding-bottom: 24px;
}

.card__header {
  display: flex;
  align-items: flex-start;
  gap: 8px;
}

.card__title {
  flex-grow: 1;
}

.card__content {
  margin-top: 16px;
}

.card__list {
  margin-top: 25px;
  max-height: 136px;
  overflow: auto;
  padding-left: 17px;
  padding-right: calc(24px - 7px);
  width: calc(100% + (24px - 7px));
}

.card__list {
  scrollbar-color: #D0D6E4 #fff;
}

.card__list::-webkit-scrollbar {
  width: 4px;
}

.card__list::-webkit-scrollbar-thumb {
  background-color: #D0D6E4;
  border-radius: 8px;
}

.card__item {
  padding: calc(16px - 2px) 0;
  font-size: 14px;
  line-height: 1.14;
  position: relative;
  list-style: upper-latin;
}

.card__item:after, .card__item:before {
  position: absolute;
  left: calc(0px - 16px);
  height: 1px;
  width: calc(100% + 16px);
  background: #EFF2F5;
}

.card__item:after {
  content: '';
  bottom: 0px;
}

.card__item:first-child:before {
  top: 0px;
  content: '';
}

.card__item-text {
  display: inline-block;
  padding-left: 9px;
}

.card__small-graph {
  margin-left: calc(0px - 24px);
  width: calc(100% + 24px * 2);
}

.graph {
  position: relative;
  z-index: 1;
  padding-top: 17px;
}

@media only screen and (max-width: 576px) {
  .graph {
    overflow: auto;
    padding: 6px 0;
  }
}

@media only screen and (max-width: 576px) {
  .graph__inner {
    min-width: 592px;
  }
}

.graph__grid, .graph__statistics-row {
  padding-left: 24px;
}

.graph__grid, .graph__col-top {
  height: 132px;
}

.graph__label {
  font-size: 12px;
}

.graph__grid {
  position: absolute;
  z-index: 0;
  width: 100%;
}

.graph__grid-label-wrapper {
  position: absolute;
  left: 0px;
  top: calc(0px - 6px);
  height: calc(100% + 6px * 2);
  display: flex;
  flex-direction: column-reverse;
  justify-content: space-between;
}

.graph__grid-label {
  text-align: right;
}

.graph__grid-col {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
}

.graph__grid-decor-line {
  width: 100%;
  height: 1px;
  background: #EFF2F5;
}

.graph__statistics-row {
  display: flex;
  position: relative;
}

.graph__col {
  flex-grow: 1;
}

.graph__col-top {
  display: flex;
  align-items: flex-end;
}

.graph__bar {
  margin: 0px auto;
  width: 16px;
  background: #F18C5C;
  height: var(--height);
  border-radius: 6px 6px 0px 0px;
}

.graph__col-label {
  text-align: center;
  margin-top: 8px;
}

.graph__history {
  display: flex;
  justify-content: center;
  gap: 16px;
  margin-top: 24px;
}

.graph__history-item {
  display: flex;
  align-items: center;
  gap: 4px;
  color: #222531;
}

.graph__square {
  border-radius: 4px;
  width: 12px;
  height: 12px;
}

.graph--style-1 {
  background: #5155C3;
}

.graph--style-2 {
  background: #597DBE;
}

.graph--style-3 {
  background: #5F99BB;
}

.graph--style-4 {
  background: #65B8B7;
}

.graph--style-5 {
  background: #6CDDB1;
}

.graph--style-6 {
  background: rgba(34, 37, 49, 0.4);
}

.graph--large-bar .graph__grid, .graph--large-bar .graph__statistics-row {
  padding-left: 50px;
}

.graph--large-bar .graph__bar {
  width: 78px;
}

.small-graph {
  background: rgba(241, 140, 92, 0.08);
  position: relative;
  z-index: 1;
}

.small-graph__grid, .small-graph__statistics {
  height: 144px;
}

.small-graph__grid {
  position: absolute;
  width: 100%;
  z-index: 0;
  padding-left: 24px;
  padding-right: calc(24px - 4px);
  top: 0px;
  left: 0px;
}

.small-graph__grid-col {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

.small-graph__grid-decor-line {
  width: 100%;
  height: 1px;
  background: #EFF2F5;
}

.small-graph__statistics {
  position: relative;
  z-index: 1;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  margin-left: calc(0px - 24px);
  width: calc(100% + 24px * 2);
}

.small-graph__bar {
  display: flex;
  flex-direction: column;
  height: var(--height);
}

