@media only screen and (min-width: 1200px) {
  .small-graph__bar-top {
    padding-bottom: 6.82%;
  }
}

.small-graph__bar-bottom {
  flex-grow: 1;
  background: #F18C5C;
}

.small-graph__history {
  padding: 10px 24px 13px;
  background: #F18C5C;
  color: #fff;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  position: absolute;
  z-index: 2;
  width: 100%;
  bottom: 0px;
  left: 0px;
}

.small-graph__percent {
  color: inherit;
  font-size: 24px;
  line-height: 1;
  font-weight: bold;
}

.small-graph__percent span {
  font-size: 14px;
}

.small-graph__history-details {
  display: flex;
  align-items: center;
  gap: 4px;
  letter-spacing: -0.06em;
}

.circle-range-graph {
  display: flex;
  align-items: center;
  flex-direction: column;
}

.circle-range-graph__active-range {
  display: block;
  stroke-dasharray: calc(var(--progress) * (224px / 100)) 224px;
  stroke-dashoffset: 0px;
  transform: translateY(33%) translateX(-15%) rotate(-112deg);
  transform-origin: center;
  color: #F18C5C;
  stroke: currentColor;
}

.circle-range-graph__value-row {
  display: flex;
  justify-content: space-between;
  max-width: 110px;
  width: 100%;
}

.circle-range-mood {
  display: flex;
  justify-content: center;
  padding-top: 5px;
  padding-bottom: 16px;
}

.circle-range-mood__wrapper {
  position: relative;
  z-index: 1;
}

.circle-range-mood__background, .circle-range-mood__progress {
  stop-opacity: 1;
}

.circle-range-mood__range {
  z-index: 1;
}

.circle-range-mood__background {
  stop-color: #EFF2F5;
}

.circle-range-mood__progress {
  stop-color: #4C57FB;
}

.circle-range-mood__icon {
  position: absolute;
  z-index: 2;
}

.circle-range-mood__icon--from, .circle-range-mood__icon--to {
  bottom: 0px;
  transform: translateY(50%);
}

.circle-range-mood__icon--half {
  top: -7px;
  left: 50%;
  transform: translateX(-50%);
}

.circle-range-mood__icon--from {
  left: -4px;
}

.circle-range-mood__icon--to {
  right: -4px;
}

.dashboard {
  padding: 32px 0px;
}

@media only screen and (min-width: 1000px) {
  .dashboard {
    padding: 64px 0px;
  }
}

.dashboard__row {
  display: flex;
  flex-direction: column;
}

@media only screen and (min-width: 1000px) {
  .dashboard__row {
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
  }
}

@media only screen and (min-width: 1000px) {
  .dashboard__title-wrapper {
    max-width: 412px;
  }
}

.dashboard__subtitle {
  margin-top: 8px;
}

@media only screen and (max-width: 1000px) {
  .dashboard__tabs-wrapper {
    margin-top: 20px;
  }
}

.dashboard__grid {
  margin-top: 20px;
}

@media only screen and (min-width: 768px) {
  .dashboard__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 16px;
  }
}

@media only screen and (min-width: 1000px) {
  .dashboard__grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 16px;
  }
}

@media only screen and (min-width: 1200px) {
  .dashboard__grid {
    margin-top: 32px;
    gap: 32px;
    grid-template-columns: repeat(4, 1fr);
  }
}

@media only screen and (max-width: 768px) {
  .dashboard__card:not(:first-child) {
    margin-top: 20px;
  }
}

@media only screen and (min-width: 1000px) and (max-width: 1200px) {
  .dashboard__card--large:last-child {
    grid-column: span 3;
  }
}

@media only screen and (min-width: 768px) {
  .dashboard__card--large {
    grid-column: span 2;
  }
}
